import React from "react";
import { redirect, useNavigate, Link } from "react-router-dom";
import JobDetail from "./JobDetail";

const JobListItem = (props) => {
    const job = props.job;
    // console.log("job: ");
    // console.log(job);
    let status_bg = "";

    if (job.status === "RUNNING") {
        status_bg = "running_bg";
    }
    if (job.status === "PENDING" || job.status === "WAITING") {
        status_bg = "pending_bg";
    }
    if (job.status === "CANCELLED") {
        status_bg = "canceled_bg";
    }
    if (job.status === "COMPLETED") {
        status_bg = "complete_bg";
    }

    return (
        <li className="job_item_row" id={`${job.id}`}>
            <Link to={`/jobs/${job.id}`}>
                <div className={`job_column job_view `}>
                    <span className="view_icon"></span>
                </div>
                <div className={`job_column job_id `}>{job.id}</div>
                <div className={`job_column job_status ${status_bg}`}>
                    {job.status}
                </div>
                <div className={`job_column job_shots`}>{job.shots}</div>
                <div className={`job_column job_circuit `}>                    
                    Circuit Detail &gt;&gt;                    
                </div>
                <div className={`job_column job_circuit_format `}>
                    {job.circuit_format}
                </div>
                <div className={`job_column job_target `}>
                    {job.target_specification}
                </div>
                <div className={`job_column job_note `}>{job.note}</div>
            </Link>
        </li>
    );
};

export default JobListItem;
